import React from 'react'

import connect from '@vkontakte/vkui-connect'
import { View } from '@vkontakte/vkui'

import './App.css'

import Home from './components/Home'

//https://vk.com/app6909581#VktXZWJBcHBHZXRVc2VySW5mb0A=

class App extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            activePanel: 'home',
            // user: { id: 9387646, first_name: 'Славик', last_name: 'Беляков', sex: 2, city: { id: 2, title: 'Санкт-Петербург' }, country: { id: 1, title: 'Россия' }, photo_100: 'https://sun9-9.userapi.com/c849020/v849020067/30b7f/GmZOQKJK1uA.jpg?ava=1', photo_max_orig: 'https://sun9-22.userapi.com/c849020/v849020067/30b7b/zLZOrrEByAc.jpg?ava=1', bdate: '6.9.1995', photo_200: 'https://sun9-50.userapi.com/c849020/v849020067/30b7e/Vd-E144O3PA.jpg?ava=1', timezone: 3 }
            user: null
        }
    }

    componentDidMount() {
        connect.send('VKWebAppInit', {})

        connect.subscribe((e) => {
            console.log(e.detail)

            switch (e.detail.type) {
            case 'VKWebAppGetUserInfoResult':
                this.setState({ user: e.detail.data })
                break
            default:
                break
            }
        })

        connect.send('VKWebAppGetUserInfo', {})

        connect.send('VKWebAppGetAuthToken', { app_id: 7105859, scope: 'friends,status' })
    }

    render() {
        const { activePanel, user } = this.state

        return (
            <View activePanel={activePanel}>
                <Home
                    id='home'
                    user={user}
                />
            </View>
        )
    }
}

export default App
